import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import useOhwell from "../../hooks/useOhwell";
import { __addOhwell } from "../../redux/modules/ohsikSlice";
import { Container, SedForm, ButtonDiv, ContentBox } from "./styles";

const Ohsik = () => {
  //value 
  const [value, onChangehandler] = useOhwell();

  //hook 
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const ohwell = useSelector((state) => state.ohsik);
  console.log(ohwell);
  // 
  const onClickAddFormHandler = () => {
    if (
      value.nickname.trim() === "" ||
      value.title.trim() === "" ||
      value.memo.trim() === ""
    ) {
      return alert(" !.");
    } else if (value.time === "" || value.time === "") {
      return alert(" !");
    }
    dispatch(__addOhwell(value));
    navigate("/alldat");
  };

  return (
    <Container>
      <SedForm>
        <div>
          <h3></h3>
          <input
            required
            name="nickname"
            value={value.nickname}
            onChange={onChangehandler}
            type="text"
          />
        </div>
        <div>
          <h3></h3>
          <input
            required
            name="title"
            value={value.title}
            onChange={onChangehandler}
            type="text"
          />
        </div>

        <div>
          <h3>(Oh-sik)</h3>
          <div>
            <select
              required
              value={value.time}
              onChange={onChangehandler}
              name="time"
            >
              <option></option>
              <option> </option>
              <option> </option>
              <option> </option>
            </select>
          </div>
        </div>
        <ContentBox>
          <h3></h3>
          <input
            required
            name="memo"
            type="text"
            value={value.memo}
            onChange={onChangehandler}
          />
        </ContentBox>
      </SedForm>

      <ButtonDiv>
        <button
          onClick={() => {
            onClickAddFormHandler(value);
          }}
        >
          
        </button>
      </ButtonDiv>
    </Container>
  );
};

export default Ohsik;
