import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { __deleteOhwell, __getOhwell } from "../../redux/modules/ohsikSlice";
import { Title, Boxes } from "./styles";

const Dats = () => {
  //hook 
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const ohwell = useSelector((state) => state.ohsik.ohwells);
  console.log(ohwell);

  //useEffect 
  useEffect(() => {
    dispatch(__getOhwell());
  }, [dispatch]);
  //handler 
  const onDeleteClickhandler = (id) => {
    const result = window.confirm("  ?");
    if (result) {
      dispatch(__deleteOhwell(id));
    } else {
      return;
    }
  };

  return (
    <>
      <Title> OH-WELL-DAT (  )</Title>

      {ohwell?.map((ohwell) => {
        return (
          <Boxes key={ohwell.id}>
            <span>
              {ohwell.time === " "
                ? ""
                : ohwell.time === " "
                ? ""
                : ""}
            </span>

            <span>{ohwell.title}</span>

            <h5>:{ohwell.nickname}</h5>

            <div>
              <p onClick={() => navigate(`/info/${ohwell.id}`)}></p>
              <button onClick={() => onDeleteClickhandler(ohwell.id)}>
                {" "}
                
              </button>
            </div>
          </Boxes>
        );
      })}
    </>
  );
};
export default Dats;
