import React from "react";
import { useNavigate } from "react-router-dom";
import { Container, Nav, Ohbox, Img } from "./styles";

const OhWell = () => {
  const navigate = useNavigate();
  return (
    <Container>
      <Nav>
        <Ohbox
          onClick={() => {
            navigate("/ohsik");
          }}
        >
          (oh~sik)
        </Ohbox>
        <Ohbox
          onClick={() => {
            navigate("/alldat");
          }}
        >
          (oh~dat)
        </Ohbox>
      </Nav>
      <Img></Img>
    </Container>
  );
};
export default OhWell;
