import React, { useState } from "react";

const useOhwell = () => {
  //value  state !
  const [ohwell, setOhwell] = useState({
    title: "",
    nickname: "",
    memo: "",
    time: "",
  });

  // 
  const handler = (e) => {
    const { name, value } = e.target;
    setOhwell({ ...ohwell, [name]: value });
  };

  return [ohwell, handler];
};

export default useOhwell;
